import React, {useState, useEffect} from 'react';
import ArticlesList from '../components/ArticlesList';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpVoteSection';
import AddCommentForm from '../components/AddCommentForm';
import articleContent from './article-content';
import NotFoundPage from './NotFoundPage';

const ArticlePage = ({ match }) => {
    // fetch('api/articles/...', {
    //     // method: 'GET',
    //     // body:
    // });

    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({upvotes:0 , comments: []}); 
    //articleInfo -> populate by sending a request to the server
    //setArticleInfo -> fx to change the value of asticleInfo
    //useState arg ({})-> initial value of articleInfo before changing its state or loading any date
    //better use a default but include the default value of properties we expect to see in the articleInfo eg. {upvotes: 0, comments[]}

    //How to make request to the backend to load articleInfo
    //use useEffect -> gives us a place to perform all the side effects of our components,
    //such as, in our fetching data and setting the state with the result

    //useEffect is triggered when the component first mounts and when the components updates or when the articleInfo changes
    //loop since we update inside the articleInf
    useEffect(()=>{
       const fetchData = async ()=>{
        const result = await fetch(`/api/articles/${name}`);
        const body = await result.json(); 
        setArticleInfo(body);
       }
        fetchData();
    },[name]);

    if (!article) return <NotFoundPage />

    const otherArticles = articleContent.filter(article => article.name !== name);
    return (
        <>
            <h1>
                {article.title}
            </h1>
            <UpvoteSection articleName = {name} upvotes = {articleInfo.upvotes} setArticleInfo = {setArticleInfo} />
      
            {article.content.map((paragraph, key) =>(
                <p key = {key}>{paragraph}</p>
            ))};
            <CommentsList comments = {articleInfo.comments} />
            <AddCommentForm articleName = {name} setArticleInfo = {setArticleInfo} />
            <h3>Other Articles:</h3>
            <ArticlesList articles={otherArticles} />
        </>
    );
}
export default ArticlePage;